



















































html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.wall {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.tile-filter {
  position: absolute;
  z-index: 999;
  top: 0;
  right: 0;
}
.tile-filter label {
  color: white;
}
.tile-filter input:focus:not([readonly])+label {
  color: white;
}
.tile-filter input:focus:not([readonly]) {
  border-bottom: 1px solid white;
}



